import React, { Component } from 'react';
// import Picture from "./picture";
import automotive from '../img/automotive2.jpg';
import people from '../img/people2.jpg';
import nature from '../img/nature2.jpg';
import architecture from '../img/architecture2.jpg';


// import photo from '../img/';

import data from '../source/data';

// import AboutMe from "./AboutMe"
// import { Dropdown } from 'semantic-ui-react'

class PC extends Component {
  constructor() {
    super();
    this.state = {
      blockPic : false,
      pictureBlock :  <section className="picture">
                            <div  onClick={this.handlerClickAuto.bind(this)} className="img first">
                                <div className="BlockImg0">
                                    <p>AUTOMOTIVE</p>
                                </div>
                                <img className="first_img" src={automotive} alt="img"></img>
                            </div>
                            <div  onClick={this.handlerClickPeople.bind(this)} className="img second">
                                <div className="BlockImg1">
                                    <p>PEOPLE</p>
                                </div>
                                <img src={people} alt="img"></img>
                            </div>
                            <div  onClick={this.handlerClickNature.bind(this)} className="img third">
                                <div className="BlockImg2">
                                    <p>NATURE</p>
                                </div>
                                <img src={nature} alt="img"></img>
                            </div>
                            <div  onClick={this.handlerClickAr.bind(this)} className="img fourth">
                                <div className="BlockImg3">
                                    <p>ARCHITECTURE</p>
                                </div>
                                <img src={architecture} alt="img"></img>
                            </div>
                          </section>
    }

    // this.handlerChange = this.handlerChange.bind(this);    
    this.handlerClick = this.handlerClick.bind(this);
    this.handlerClickAuto = this.handlerClickAuto.bind(this);
    this.handlerClickNature = this.handlerClickNature.bind(this);
    this.handlerClickAr = this.handlerClickAr.bind(this);

  }

  // handlerChange (event) {
  //   this.setState({
      
  //   })
  // }


  handlerClickAr(e) {
    let section = data.Ar.map((item) => 
      <div className="pic" key={item.id}>
          <img className="first_img" src={item.link} alt="img"></img>
      </div>  
    ) 
    this.setState({
      blockPic : <section className="blockPhoto">
                    {section}
                  </section>,
    })
  }

  handlerClickAuto(e) {
    let section = data.automotive.map((item) => 
      <div className="pic" key={item.id}>
          <img className="first_img" src={item.link} alt="img"></img>
      </div>  
    ) 
    this.setState({
      blockPic : <section className="blockPhoto">
                    {section}
                  </section>,
    })
  }

  handlerClickNature(e) {
    let section = data.nature.map((item) => 
      <div className="pic" key={item.id}>
          <img className="first_img" src={item.link} alt="img"></img>
      </div>  
    )
    this.setState({
      blockPic : <section className="blockPhoto">
                    {section}
                  </section>,
    })
  }

  handlerClickPeople(e) {
    alert("NOT NOW");
  }

  handlerClick(e) {
    console.log(e.target)
          this.setState({
            blockPic : false
          })
  }

  render () {
    const {blockPic, pictureBlock} = this.state;
    if (blockPic) {
      return (      
        <>
          <div className="main">
            <div className="menu">
              <header className="blockMenu">
                <div className="site-logo">
                  <a href="/MOBILE">ANDREY MARKUSHEVSKY</a>
                </div>
                <nav>
                  <ul className="group" >
                    <li onClick={this.handlerClick}>Works</li>
                    <li onClick={this.handlerClickAuto}>Automotive</li>
                    <li onClick={this.handlerClickPeople}>People</li>
                    <li onClick={this.handlerClickNature}>Nature</li>
                    <li onClick={this.handlerClickAr}>Architecture</li>
                  </ul>
                  <div>
                    <p>About Me</p>
                  </div>
                  <div>
                    <p>Contact</p>
                  </div>
                </nav>
              </header>
            </div>
            {blockPic}
          </div>
        </>
      )} else if (blockPic === false) {
      return (      
        <>
          <div className="main">
            <div className="menu">
              <header className="blockMenu">
                <div className="site-logo">
                  <a href="/MOBILE">ANDREY MARKUSHEVSKY</a>
                </div>
                <nav>
                  <ul className="group" >
                    <li onClick={this.handlerClick}>Works</li>
                    <li onClick={this.handlerClickAuto}>Automotive</li>
                    <li onClick={this.handlerClickPeople}>People</li>
                    <li onClick={this.handlerClickNature}>Nature</li>
                    <li onClick={this.handlerClickAr}>Architecture</li>
                  </ul>
                  <div>
                    <p>About Me</p>
                  </div>
                  <div>
                    <p>Contact</p>
                  </div>
                </nav>
              </header>
            </div>
            {pictureBlock}
          </div>
        </>
      )
    }
  }
}
 
export default PC;