import React, { Component } from 'react';
// import Picture from "./picture"
import data from '../source/data';
import automotive from '../img/automotive2.jpg';
import people from '../img/people2.jpg';
import nature from '../img/nature2.jpg';
import architecture from '../img/architecture2.jpg';

class User extends Component {
    constructor() {
      super();
      this.state = {
            header : <header className="header_mobile">
                        <div id="lines" onClick={this.handlerClick.bind(this)}></div>
                        <p className="site-logo mobile">Andrey Markushevsky</p>        
                    </header>,
            menu :  <nav className='blockMenuMobile'>
                        <ul className="group" >
                        <li onClick={this.handlerClick}>Works</li>
                        <li onClick={this.handlerClickAuto}>Automotive</li>
                        <li onClick={this.handlerClickPeople}>People</li>
                        <li onClick={this.handlerClickNature}>Nature</li>
                        <li onClick={this.handlerClickAr}>Architecture</li>
                        </ul>
                        <div>
                        <p>About Me</p>
                        </div>
                        <div>
                        <p>Contact</p>
                        </div>
                    </nav>,
            pictureBlock :  <section className="picture">
                                <div className="img first">
                                    <div className="BlockImg0">
                                        <p>AUTOMOTIVE</p>
                                    </div>
                                    <img className="first_img" src={automotive} alt="img"></img>
                                </div>
                                <div className="img second">
                                    <div className="BlockImg1">
                                        <p>PEOPLE</p>
                                    </div>
                                    <img src={people} alt="img"></img>
                                </div>
                                <div className="img third">
                                    <div className="BlockImg2">
                                        <p>NATURE</p>
                                    </div>
                                    <img src={nature} alt="img"></img>
                                </div>
                                <div className="img fourth">
                                    <div className="BlockImg3">
                                        <p>ARCHITECTURE</p>
                                    </div>
                                    <img src={architecture} alt="img"></img>
                                </div>
                            </section>,
            activeForm : false,
      }
      this.handlerClick = this.handlerClick.bind(this);
    }

    handlerClick (e) {
        console.log(document.querySelector('#lines').getAttribute("style"))
        document.querySelector('#lines').getAttribute("style") ? document.querySelector('#lines').removeAttribute("style") : document.querySelector('#lines').setAttribute("style", 'transform: rotate(90deg); height: 20px; width: 20px;');
        this.setState({
            activeForm : this.state.activeForm ? false : true
        })
    }

    render () {
        const {header, activeForm, menu, pictureBlock} = this.state;
        if (!activeForm) {
            return (
                <>
                    {header}
                    {pictureBlock}
                </>
            )
        } else {
            return (
                <>
                    {header}
                    {menu}
                    {pictureBlock}
                </>
            )
        }
    }
}

export default User;